import React, { useState, useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import { Fade } from "@material-ui/core";
import HomeRedirect from "./pages/HomeRedirect";
import HomeIconUrl from "./assets/home.png";
import ProjectsIconUrl from "./assets/projects.webp";
import ContactIconUrl from "./assets/contact.png";
import shortid from "shortid";
import {
	CodeOutlined as ProjectsIcon,
	HomeOutlined as HomeIcon,
	PermContactCalendarOutlined as ContactIcon,
} from "@material-ui/icons";
import asyncRoute from "./components/asyncRoute";
import "./App.css";
const HomePage = asyncRoute(() => import("./pages/homePage/HomePage"));
const ErrorPage = asyncRoute(() => import("./pages/errorPage/ErrorPage"));

function App() {
	const [isReady, setReady] = useState(false);

	useEffect(() => {
		setReady(true);
	}, []);

	function getRandomKey() {
		return shortid.generate();
	}
	return (
		<Fade in timeout={1000}>
			<div
				className="App"
				style={{ visibility: isReady ? "visible" : "hidden" }}
				key={getRandomKey()}
			>
				<div className="App-inner" key={getRandomKey()}>
					<Fade in timeout={2000}>
						<div className="App-inner-inner" key={getRandomKey()}>
							<div className="app-bar">
								<h1>Portfolio</h1>
								<nav className="app-bar-nav">
									<div className="app-bar-nav-item">
										<HomeIcon className="nav-image" />
										Home
									</div>
									<div className="app-bar-nav-item">
										<ProjectsIcon className="nav-image" />
										Projects
									</div>
									<div className="app-bar-nav-item">
										<ContactIcon className="nav-image" />
										Contact
									</div>
								</nav>
							</div>

							<Switch>
								<Route path="/home" exact component={HomePage} />
								<Route path="/" exact component={HomeRedirect} />
								<Route path="/" component={ErrorPage} />
							</Switch>
						</div>
					</Fade>
				</div>
			</div>
		</Fade>
	);
}

export default App;
